"use client";

import { useState } from "react";

type Props = {
  value?: string;
  onSubmit?: (ticker: string) => void; // ✅ Make it optional to prevent crash
};

export default function SingleTickerSearch({ value = "", onSubmit }: Props) {
  const [input, setInput] = useState(value);

/*************  ✨ Windsurf Command ⭐  *************/
  /**
   * When the user presses the Enter key, prevent the default form submission and
   * instead call `onSubmit` with the trimmed and uppercased input value. If the
   * input is empty, do nothing.
   *
   * @param e - The KeyboardEvent
   */
/*******  516b1d7c-ce28-4acd-a19f-f0e4e01126f9  *******/  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter") {
      e.preventDefault();
      const clean = input.trim().toUpperCase();
      if (clean && typeof onSubmit === "function") {
        onSubmit(clean);
      } else {
        console.warn("❌ onSubmit is not defined for this <SingleTickerSearch>");
      }
    }
  };

  return (
    <input
      type="text"
      placeholder="Enter ticker (e.g., AAPL)"
      value={input}
      onChange={(e) => setInput(e.target.value)}
      onKeyDown={handleKeyDown}
      className="bg-zinc-800 text-white p-3 rounded-lg w-full placeholder:text-gray-400"
    />
  );
}
